<template>
    <view :class="['s-avatar', isShape]" :style="{ height, width }">
        <slot v-if="!src">
            <text  :style="{ fontSize }"></text>
        </slot>
        <image
                class="s-avatar__target"
                v-else
                :src="src"
                @error="handleError"
                @load="handleLoad"
        ></image>
    </view>
</template>

<script>
    export default {
        name:"SunnyAvatar",
        props: {
            src: String,
            mode: String,
            lazyLoad: Boolean,
            size: {
                type: Number,
                default: 80
            },
            shape: {
                type: String,
                default: 'circle'
            }
        },

        computed: {
            isShape() {
                return `s-avatar--${this.shape}`;
            },

            height() {
                return this.size + 'rpx';
            },

            width() {
                return this.size + 'rpx';
            },

            fontSize() {
                return this.size / 1.7 + 'rpx';
            }
        },

        methods: {
            handleLoad(e) {
                this.$emit('error', e);
            },

            handleError(e) {
                this.$emit('load', e);
            }
        }
    };
</script>
<style lang="scss" scoped>
    .s-avatar {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #c0c4cc;
        font-size: 28rpx;
        color: #fff;
        overflow: hidden;
        box-sizing: border-box;

        &--circle {
             border-radius: 100%;
         }

        &--square {
             border-radius: 10rpx;
         }

        &__target {
             height: 100%;
             width: 100%;
         }
    }

    .s-avatar-group {
        display: flex;

        .s-avatar {
            margin-left: -20rpx;
            border: 4rpx solid rgba(255, 255, 255, 0.2);
            position: relative;
        }

        &__more {
             background-color: $color-primary;
             display: flex;
             justify-content: center;
             align-items: center;
             height: 100%;
             width: 100%;

            &::before {
                 content: '+';
                 position: relative;
                 top: -2rpx;
                 right: 2rpx;
             }
        }
    }

</style>
